import React from 'react'
import PricingFive from '../../../elements/price/element/price5';
import { Container, Row, Col } from 'reactstrap'

var settings = {
    dots: true,
    arrows: false,
    infinite: true,
    speed: 500,
    arrows: false,
    slidesToShow: 3,
    autoplay: true,
    centerMode: true,
    centerPadding: '0',
    slidesToScroll: 1,
    responsive: [
        {
            breakpoint: 992,
            settings: { slidesToShow: 1 }
        }
    ]
};


const PricingResume2 = [
    {
        icon: "assets/images/OTF/icons/Premier.png",
        iconW: "assets/images/OTF/icons/PremierW.png",
        title: "Orange Premier",
        feature1: "Unlimited classes",
        feature2: 'Reduced rate for families'
    },
    {
        icon: "assets/images/OTF/icons/Orange-elite.png",
        iconW: "assets/images/OTF/icons/eliteW.png",
        title: "Orange Elite",
        feature1: "8 classes/month",
        feature2: 'Extra classes at reduced rate'
    },
    {
        icon: "assets/images/OTF/icons/Basic.png",
        iconW: "assets/images/OTF/icons/BasicW.png",
        title: "Orange Basic",
        feature1: "4 classes/month",
        feature2: 'Extra classes at reduced rate'
    }
];

const Pricing = () => (
    <section className="gym pricing set-relative" id="plan">
        <Container>
            <Row>
                <Col md="10" className="offset-md-1">
                    <h2 className="oftHeading text-dark">Letâ€™s talk memberships</h2>
                </Col>
                <Col lg="12" md="8" className="offset-md-2 offset-lg-0">
                    <Slider className="pricing-slider price-margin" {...settings}>
                        {PricingData.map((item, i) => {
                            return (
                                <div className="item" key={i}>
                                    <div className="price-container shadows text-center">
                                        <div className="service-feature ">
                                            <div className="feature-text">
                                                <img alt="" className="image-top" src={item.img} />
                                                <h3 className="text-white feature-text-heading">{item.heading}</h3>
                                            </div>
                                        </div>
                                        <div>
                                            <div className="price-feature-container ">
                                                <div className="price-features">
                                                    <h5 className="price-feature">{item.feature1}</h5>
                                                    <h5 className="price-feature">{item.feature2} </h5>
                                                    <h5 className="price-feature">{item.feature3} </h5>
                                                </div>
                                                <div className="price-value"><h6 className="price">$<span className="large">{item.price}</span>/month</h6>
                                                </div>
                                                <a className="btn btn-default btn-gradient" href="#">more</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </Slider>
                </Col>
                <PricingFive />
            </Row>
        </Container>
    </section>
)


export default Pricing;